  /* The switch - the box around the slider */
  .switch {
    position: relative;
    display: inline-block;
    width: 16px;
    height: 8px;
    left: 7px;
    margin-right: 5px;
    margin-left: 5px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    display: none;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 8px;
    width: 8px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
    box-shadow: -1.33333px 0px 1.33333px rgba(0, 0, 0, 0.1);
  }
  
  input:checked + .slider:before {
    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: -1.33333px 0px 1.33333px rgba(0, 0, 0, 0.1);
  }
  
  input:focus + .slider:before {
    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: -1.33333px 0px 1.33333px rgba(0, 0, 0, 0.1);
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(8px);
    -ms-transform: translateX(8px);
    transform: translateX(8px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 5px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
  
